.inpContainer {
    position: relative;
}
.inpContainer label {
    cursor: pointer;

    position: relative;
    z-index: 9;

    display: block;

    padding: 5px 10px 5px 34px;

    font-size: 17px;
    font-weight: 600;
}
.inpContainer input[type=radio] {
    position: absolute;

    visibility: hidden;
}

.check {
    position: absolute;
    z-index: 5;
    top: 50%;
    left: 10px;
    transform: translate(0, -50%);

    display: block;

    width: 16px;
    height: 16px;

    border: 2px solid #525252;
    border-radius: 100%;

    -webkit-transition: border .25s linear;
    transition: border .25s linear;
}

.inpContainer input[type=radio]:hover .check {
    border: 2px solid rgba(0, 71, 255, 0.81);
}

.check::before {
    content: '';

    position: absolute;
    top: 4px;
    left: 4px;

    display: block;

    width: 8px;
    height: 8px;
    margin: auto;

    border-radius: 100%;

    -webkit-transition: background 0.25s linear;
    transition: background 0.25s linear;
}

.inpContainer input[type=radio]:checked ~ .check {
    border: 2px solid rgba(0, 71, 255, 0.81);
}

.inpContainer input[type=radio]:checked ~ .check::before{
    background: rgba(0, 71, 255, 0.81);
}